.status-image__wrapper {
  position: relative;
  width: 90vw;  
  margin: 0 auto;
}

.status-image{
  position: absolute;
  top: 0;
  left: 0; 
}

.status-image, .status-image__placeholder {
  width: 100%;  
  height: 75vh;
  border: none;
  object-fit: cover; 
  margin-bottom: 1rem;
  border-radius: 0.5rem; 
}

.status-image__placeholder {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.status-image__placeholder.hidden{
  visibility: hidden;
}

.status-image__size-small {
  height: 50vh;
}

.status-image__size-medium {
  height: 63vh;
}




.fade-in {
	opacity: 1;
	-webkit-animation-name: fadeInOpacity;
	        animation-name: fadeInOpacity;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in;
	        animation-timing-function: ease-in;
	-webkit-animation-duration: 0.25s;
	        animation-duration: 0.25s;
}

@-webkit-keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
.fade-out {
	opacity: 1;
	-webkit-animation-name: fadeOutOpacity;
	        animation-name: fadeOutOpacity;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-out;
	        animation-timing-function: ease-out;
	-webkit-animation-duration: 0.25s;
	        animation-duration: 0.25s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes fadeOutOpacity {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		visibility: hidden;
	}
}

@keyframes fadeOutOpacity {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		visibility: hidden;
	}
}
body {
  box-sizing: border-box;
  background-color: crimson;
}

.App {
  font-family: sans-serif;
  text-align: center;
  font-family: "Quicksand", sans-serif;
}

h1 {
  color: white;
  text-transform: uppercase;
  font-family: "Quicksand", sans-serif;
  font-weight: bolder;
}

.reservation-time__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.reservation-time__item {
  color: white;
  background: rgba(0, 0, 0, 0.3);
  margin: 0.2rem;
  padding: 0.7rem;
  border-radius: 0.2rem;
}

.no-reservations {
  font-size: 2rem;
  color: white;
}

.no-reservations span {
  font-weight: bold;
  color: black;
  background-color: white;
  padding: 0.1rem;
}

